* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.timer{
  display: flex;
  gap: 25px;
  margin-top: 50px;
  /*border: solid 1px red;*/
}

.value{
  font-size: 35px;
}

.field{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 30px;
}

.select{
  display: flex;
  gap: 25px;
}

#datetime-picker{
  width: 400px;
}

div.select > button{
  width: 100px;
  padding: 10px 20px;
  font-size: 18px;
  font-family: sans-serif;
  font-weight: 500;
}
