* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  gap: 25px;
  margin-top: 50px;
  display: flex;
}

.value {
  font-size: 35px;
}

.field {
  flex-direction: column;
  align-items: center;
  font-size: 30px;
  display: flex;
}

.select {
  gap: 25px;
  display: flex;
}

#datetime-picker {
  width: 400px;
}

div.select > button {
  width: 100px;
  padding: 10px 20px;
  font-family: sans-serif;
  font-size: 18px;
  font-weight: 500;
}

